/* jshint ignore:start */

define('livecoding-demo/config/environment', ['ember'], function(Ember) {
  var prefix = 'livecoding-demo';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("livecoding-demo/tests/test-helper");
} else {
  require("livecoding-demo/app")["default"].create({"name":"livecoding-demo","version":"0.0.0.5e1c81d0"});
}

/* jshint ignore:end */
